<template>
  <modal>
    <div slot="header">
      <h3></h3>
    </div>

    <div slot="body" v-if="!editMode">
      <div class="option">
        <button class="modal-default-button" @click="editMode = true"></button>
      </div>

      <div class="form-container">
        <div>: {{ eventData.summary }}</div>
        <div>: {{ eventData.start.dateTime || eventData.start.date }}</div>
        <div>: {{ eventData.location }}</div>
        <div>: {{ eventData.description }}</div>
      </div>
    </div>

    <div slot="body" v-if="editMode">
      <div class="option">
        <button class="modal-default-button" @click="editMode = false"></button>
      </div>

      <div class="form-container">
        <div>
          <span>:</span>
          <input v-model="eventData.summary">
        </div>
        <div>
          <span>:</span>
          <input v-if="eventData.start.date" v-model="eventData.start.date">
          <input v-if="eventData.start.dateTime" v-model="eventData.start.dateTime">
        </div>
        <div>
          <span>:</span>
          <input v-model="eventData.location">
        </div>
        <div>
          <span>:</span>
          <textarea v-model="eventData.description"></textarea>
        </div>
      </div>
    </div>

    <div slot="footer" v-if="!editMode">
      <button class="modal-default-button" @click="$emit('close')"></button>
    </div>

    <div slot="footer" v-if="editMode">
      <button class="modal-default-button" @click="updateEvent()"></button>
      <button class="modal-default-button" @click="$emit('close')"></button>
    </div>
  </modal>
