<script>
import modal from "./Modal.vue";
// import axios from "axios";

export default {
  components: {
    modal
  },

  props: {
    event: Object,
    createMode: Boolean
  },

  data() {
    return {
      editMode: false,
      eventData: Object.assign({}, this.event)
    };
  },

  methods: {
    async updateEvent() {
      axios
        .patch(`/api/calendar/event/${this.eventData.id}`, {
          event: this.eventData
        })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  mounted() {
    // this.eventData = this.event;
  }
};
</script>

